.Community {
	color: #000;
	.communityBanner {
		width: 100%;
		height: 200px;
		border-bottom: 5px solid #000;
		margin: 100px 0px;
		.bannerTitle {
			font: bold 70px/1 'Arapey';
		}
		.bannerText {
			font: 40px/1 'Arapey';
		}
	}
	.Box {
		display: flex;
		justify-content: space-between;
		.inputBox {
			width: 49%;
			height: 500px;
			padding: 30px;
			border: 1px solid #000;
			text-align: center;
			h2 {
				font: bold 30px/1 'Arapey';
				margin-bottom: 10px;
			}
			input[type='text'] {
				width: 200px;
				height: 40px;
				padding: 10px;
				margin: 20px 0px;
				text-align: center;
				border: none;
				border-bottom: 1px solid #000;
			}
			textarea {
				width: 80%;
				resize: none;
				text-align: center;
			}
			button {
				width: 150px;
				height: 30px;
				margin-top: 20px;
				margin-right: 20px;
				&:nth-of-type(2){
					margin-right: 0px;
				}
			}
			span {
				display: block;
				width: 100%;
				margin-top: 20px;
				font: 18px/1 'Arapey';
			}
		}

		.showBox {
			width: 49%;
			height: 100%;
			border: 1px solid #bbb;
			padding: 20px;
			.showBoxTitle {
				text-align: center;
				font: bold 30px/1 'Arapey';

				margin-bottom: 30px;
			}
			article {
				border-bottom: 2px solid #000;
				margin-bottom: 10px;
				padding: 20px;
				h2 {
					width: 100%;
					color: #fff;
					margin-bottom: 10px;
					padding: 5px;
					font-family: 'Arapey';
					background: #000;
					box-shadow: 8px 8px 2px 1px #ddd;
				}
				p {
					font: 20px/1 'Arapey';
					padding: 20px;
				}
				.btns {
					width: 100%;

					button {
						width: 100px;
						height: 20px;
						margin-right: 10px;
						font: 15px/1 'Arapey';
						color: #fff;
						background: #000;
						box-shadow: 8px 8px 2px 1px #ddd;
					}
				}
			}
		}
	}
}


@media screen and (max-width: 1024px) {
	.Community {
	
		.Box {
			flex-wrap: wrap;
			.inputBox {
				textarea {
					width: 80%;
				}
				button {
					width: 100px;
					height: 30px;
					margin-top: 20px;
					margin-right: 20px;
					&:nth-of-type(2){
						margin-right: 0px;
					}
				}
				span {
					font: 15px/1 'Arapey';
				}
			}
	
			.showBox {

				.showBoxTitle {
					margin-bottom: 10px;
				}
				article {
					p {
						padding: 10px;
					}
				}
			}
		}
	}

}

